import { LitElement, css, html } from 'lit';
import { property, customElement } from 'lit/decorators.js';

@customElement('app-share-forbonus')
export class AppMain extends LitElement {

  @property() title = 'Hai, tolong bantu ingatkan saya untuk menabung hingga 100.000.000 Koin, dan Anda juga bisa mendapatkan hadiah susun kata solver.Klik link di bawah ini untuk membantu saya.';
  @property() dataurl = 'https://celenganku.github.io/';
  
  //@property() reftobonus: any = null;
  
  static get styles() {
    return css`

    fluent-card {
      padding: 0px 18px 18px;
    }

    @media(prefers-color-scheme: light) {
      fluent-card {
        --fill-color: #edebe9;
      }
    }

    @media(prefers-color-scheme: dark) {
      fluent-card {
        --fill-color: #4e4e4e;
        color: white;
        border: none;
      }
    }

    @media (min-width: 1024px) {
      fluent-card {
        width: 54%;
      }
    }
    `;
  }

  constructor() {
    super();
  }
  
  async firstUpdated() {
	console.log('firstUpdated');
	//this.reftobonus = (<HTMLElement> this.shadowRoot?.querySelector('fluent-anchor#reftobonus'));
	  document.getElementById("sharerelemtns")?.setAttribute("style", "display:block;");
  }
  /*
  btncliked(){

  }*/

  render() {
    return html`
      <app-header ?enableBack="${false}"></app-header>

      <div>
		<h2>Celenganku Sharing Dulu</h2>
		<fluent-anchor href="${(import.meta as any).env.BASE_URL}susunkata" appearance="accent" style="display:none;" id="reftobonus">Klik Untuk Lanjut</fluent-anchor>
		<fluent-card>
			<center><img alt="App Logo" src="/assets/icons/icon_512.png" style="width: 100%;height: auto;"></center>
		</fluent-card>
      </div>
    `;
  }
}

